import {Component, OnInit} from '@angular/core';

declare interface TableData {
  headerRow: string[];
  dataRows: string[][];
}

@Component({
  selector: 'app-growler-list',
  templateUrl: './growler-list.component.html',
  styleUrls: ['./growler-list.component.css']
})
export class GrowlerListComponent implements OnInit {
  public tableData: TableData;

  constructor() {
  }

  ngOnInit() {
    const growlers = [];
    for (let i = 0; i < 5; i++) {
      growlers.push(['Malvadeza Pub1', '+5551995797395', 'beba@malvadeza.com', 'https://malvadeza.com']);
    }
    this.tableData = {
      headerRow: ['Nome', 'Telefone', 'Email', 'Website'],
      dataRows: growlers
    };
  }

}



